export default {
  data() {
    return {
      activeName: "",
      ruleForm: {
        count: "",
        coins_range: "",
        total_coin: ""
      },
      rules: {
        count: [{ required: true, message: "不可以为空", trigger: "blur" }],
        coins_range: [{ required: true, message: "不可以为空", trigger: "blur" }],
        total_coin: [{ required: true, message: "不可以为空", trigger: "blur" }],
      },
      namelist: [],
      id: 0,
      keys: "",
      resData: {},
      loading: false
    };
  },

  async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.GetServerConfig({
      key: "ddz_robot_control.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.resData = res;
    this.namelist = Object.keys(res);
    this.ruleForm = res[this.namelist[0]];
    this.activeName = this.namelist[0];
    // console.log(this.resData, this.ruleForm);
  },

  methods: {
    handleClick(tab) {
      this.namelist.forEach(item => {
        if (item === tab.name) {
          this.ruleForm = this.resData[item];
        }
      });
    },

    submitForm(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          console.log(this.resData, this.ruleForm);

          if (type === 1) {
            //put
            let { data } = await this.$http.HallFunConfig.PutServerConfig({
              keys: this.keys,
              values: JSON.stringify(this.resData),
              id: this.id
            });
            // console.log(data);

            if (data.code === 1 && data.msg === "ok") {
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }
          } else if (type === 2) {
            //post
            this.loading = true;
            //发送post
            let { data } = await this.$http.HallFunConfig.PostServerConfig({
              keys: this.keys,
              values: JSON.stringify(this.list),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送到服务器配置成功!"
              });
            }
          }
        } else {
          console.log("error");
          return false;
        }
      });
    }
  }
};



